.homeWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;

  .homeContainer {
    display: flex;
    flex-direction: column;

    .homeHeader {
      display: flex;
      align-items: center;
      width: 100vw;
      height: 10vh;
      background-color: #2b71ad;
      box-shadow: 0px 3px 5px 2px rgba(34, 60, 80, 0.2);

      .logo * {
        fill: white;
      }

      .logo {
        margin-left: 30px;
      }
    }

    .homeContent {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100vw;
      height: 90vh;

      .monitoringContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-right: 10px;

        .monitoringContent {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 12vh;
          width: 6vw;
          background-color: #357fbe;
          border-radius: 5px;
          cursor: pointer;
          box-shadow: 0 5px 10px 2px rgba(34, 60, 80, 0.2);

          &:hover {
            background-color: rgba(53, 127, 190, 0.9);
          }

          &:active {
            background-color: #357fbe;
            box-shadow: 0 3px 5px 2px rgba(34, 60, 80, 0.2);
          }

          .monitoring * {
            fill: white;
          }
        }
        .itemName {
          display: block;
          font-size: 18px;
          font-weight: 600;
          margin-top: 10px;
          font-family: "Book Antiqua";
        }

      }

    }
  }
}